.icon {
  display: grid;
  font-size: 2.2em;
  justify-items: center;
  -ms-flex-align: center;
      align-items: center;
}

.icon-1 {
  grid-row: 2 / 3;
}

.icon-2 {
  grid-row: 3 / 4;
}

.icon-3 {
  grid-row: 4 / 5;
}

.icon-4 {
  grid-row: 5 / 6;
}

.icon:hover {
  cursor: pointer;  
}

.icon:hover .fas {
  font-size: 1.4em;
  -webkit-transition: all .2s cubic-bezier(0.39, 0.575, 0.565, 1);
  -o-transition: all .2s cubic-bezier(0.39, 0.575, 0.565, 1);
  transition: all .2s cubic-bezier(0.39, 0.575, 0.565, 1);
}

.iconSelected .fas{
  font-size: 1.4em;
  -webkit-transition: all .1s cubic-bezier(0.39, 0.575, 0.565, 1);
  -o-transition: all .1s cubic-bezier(0.39, 0.575, 0.565, 1);
  transition: all .1s cubic-bezier(0.39, 0.575, 0.565, 1);
}

.notSelected .fas{
  font-size: 1em;
  opacity: 0.6;
}

@media (max-width: 800px) {
  .icon {
    font-size: 2.1em;
  }

  .icon-1 {
    grid-row: unset;
    grid-column: 1 / 2;
  }
  .icon-2 {
    grid-row: unset;
    grid-column: 2 / 3;
  }
  .icon-3 {
    grid-row: unset;
    grid-column: 3 / 4;
  }
  .icon-4 {
    grid-row: unset;
    grid-column: 4 / 5;
  }
}



