.container {
  padding: 5% 5em;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-row-gap: 2.1em;
  -ms-flex-align: center;
      align-items: center;
  justify-self: center;
  -ms-flex-pack: center;
      justify-content: center;
}

figure {
  border-radius: 1em;
  padding: 1em;
  opacity: 0.6;
}

figure:hover {
  cursor: pointer;
  -webkit-box-shadow: 0px 0px 3px 0px rgba(43, 65, 77, 0.5);
          box-shadow: 0px 0px 3px 0px rgba(43, 65, 77, 0.5);  
  -webkit-transform: translateY(-4px);  
      -ms-transform: translateY(-4px);  
          transform: translateY(-4px);
  opacity: 1;
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

img {
  width: 70%;
  height: 70px;
}

@media (max-height: 800px) and (max-width: 1300px) {
  .container {
    padding: 10% 5em;
    grid-row-gap: 1em;
  }

  figure {
    padding: 0.3em 0.2em;
  }

  img {
    width: 100%;
    height: 70px;
  }
}



