#page4 {
  display: grid;
  grid-template-rows: repeat(4, calc(25vh - 2.5em));
  grid-template-columns: repeat(4, calc(25vw - 2.5em));
  grid-gap: 3em;
  -ms-flex-align: center;
      align-items: center;
}

.card {
  background-color: #fff;
  -webkit-box-shadow: 0px 0px 3px 0px rgba(43, 65, 77, 0.5);
          box-shadow: 0px 0px 3px 0px rgba(43, 65, 77, 0.5);  
  border-radius: 3px;
  padding: 2em 0;
  text-decoration: none;
  font-weight: 100;
  color: var(--main-color);
  opacity: 0.6;
}

.card:hover {
  cursor: pointer;
  -webkit-transform: translateY(-4px);
      -ms-transform: translateY(-4px);
          transform: translateY(-4px);
  opacity: 0.8; 
  -webkit-box-shadow: 0px 0px 3px 0px var(--main-color); 
          box-shadow: 0px 0px 3px 0px var(--main-color);
  -webkit-transition: all 0.2s ease-in-out;
  -o-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.logoStyle {
  width: 100%;
  height: 70px;
  padding-bottom: 1.2em;
}

.card-1 {
  grid-row: 2 / 3;
  grid-column: 2 / 3;
}

.card-2 {
  grid-row: 3 / 4;
  grid-column: 3 / 4;
}

.card-3 {
  grid-row: 3 / 4;
  grid-column: 2 / 3;
}

.card-4 {
  grid-row: 2 / 3;
  grid-column: 3 / 4;
}

@media(max-width: 800px) {
  #page4 {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
  }

  .card-1 {
    grid-row: span 2;
    grid-column: 2 / 3;
  }

  .card-2 {
    grid-row: span 3;
    grid-column: 2 / 3;
  }

  .card-3 {
    grid-row: span 4;
    grid-column: 2 / 3;
  }

  .card-4 {
    grid-row: span 5;
    grid-column: 2 / 3;
  }
}






